import { Fragment, useContext, useState } from "react"
import CartContext from "../../store/cart-contex"
import Modal from "../UI/Modal"
import styles from "./Cart.module.css"
import CartItem from "./CartItem"
import Checkout from "./Checkout"
import classes from "./Checkout.module.css"

const Cart = (props) => {
    const cartCtx = useContext(CartContext)
    const [isCheckout, setIsCheckout] = useState(false)
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [didSubmit, setDidSubmit] = useState(false)
    const [submitError, setSubmitError] = useState(null)
    const [hasError, setHasError] = useState(false)
    const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`
    const hasItems = cartCtx.items.length > 0

    const cartItemAddHandler = (item) => {
        cartCtx.addItem({ ...item, amount: 1 })
    }

    const cartItemRemoveHandler = (id) => {
        cartCtx.removeItem(id)
    }

    const orderHandler = () => {
        setIsCheckout(true)
    }

    const submitOrderHandler = async (userData) => {
        setIsSubmitting(true)
        try {
            const response = await fetch(
                "https://http-react-2a43d-default-rtdb.asia-southeast1.firebasedatabase.app/orders.json",
                {
                    method: "POST",
                    body: JSON.stringify({
                        user: userData,
                        orderedIems: cartCtx.items,
                    }),
                }
            )

            if (!response.ok) {
                throw new Error(
                    "Something Went Wrong. Please Check Your Internet Connection!"
                )
            }
            setIsSubmitting(false)
            setDidSubmit(true)
            cartCtx.clearCart()
        } catch (error) {
            setIsSubmitting(false)
            setHasError(true)
            setSubmitError(error.message)
        }
    }

    const cartItem = (
        <ul className={styles["cart-items"]}>
            {cartCtx.items.map((item) => (
                <CartItem
                    key={item.id}
                    name={item.name}
                    amount={item.amount}
                    price={item.price}
                    onRemove={cartItemRemoveHandler.bind(null, item.id)}
                    onAdd={cartItemAddHandler.bind(null, item)}
                />
            ))}
        </ul>
    )

    const modalAcion = (
        <div className={styles.actions}>
            <button className={styles["button--alt"]} onClick={props.onClose}>
                Close
            </button>
            {hasItems && (
                <button className={styles.button} onClick={orderHandler}>
                    Order
                </button>
            )}
        </div>
    )

    const checkoutButtons = (
        <div className={classes.actions}>
            <button type="button" onClick={props.onClose}>
                Cancel
            </button>
            <button className={classes.submit} type="submit" form="141">
                Confirm
            </button>
        </div>
    )

    const cartModalContent = (
        <Fragment>
            {cartItem}
            <div className={styles.total}>
                <span>Total Amount</span>
                <span>{totalAmount}</span>
            </div>
            {isCheckout && (
                <Checkout
                    onConfirm={submitOrderHandler}
                    onCancel={props.onClose}
                />
            )}
            {!isCheckout && modalAcion}
            {isCheckout && checkoutButtons}
        </Fragment>
    )

    const isSubmittingModalContent = <p>Sending Order Data...</p>

    const didSubmitModalData = (
        <Fragment>
            <p style={{ color: "green" }}>Successfully Submited the Order!</p>
            <div className={styles.actions}>
                <button
                    className={styles["button"]}
                    style={{ backgroundColor: "green" }}
                    onClick={props.onClose}
                >
                    Close
                </button>
            </div>
        </Fragment>
    )

    const submitErrorModalContent = (
        <Fragment>
            <p style={{ color: "red" }}>{submitError}</p>
            <div className={styles.actions}>
                <button className={styles["button"]} onClick={props.onClose}>
                    Close
                </button>
            </div>
        </Fragment>
    )

    return (
        <Modal onClose={props.onClose}>
            {!isSubmitting && !didSubmit && cartModalContent}
            {isSubmitting && isSubmittingModalContent}
            {!isSubmitting && didSubmit && didSubmitModalData}
            {hasError && submitErrorModalContent}
        </Modal>
    )
}

export default Cart
